
// bibliotek
import { Link } from "react-router-dom";

import { Navigation, Pagination, A11y } from 'swiper/modules';
import { Swiper, SwiperSlide, useSwiper } from 'swiper/react';
import ParagrafHome from "../../componets/paragrafHome/ParagrafHome";

//icon
import prewArrow from "../../asset/img/icon/arrow  right_.svg";
import nextArrow from "../../asset/img/icon/arrow left_.svg";

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

//style
import "./recommendationSwiper.scss";

export default function RecommendationSwiper({props}) {
    return (
        <div className="recommendationSwiper__wrapper swiper__wrapper _body__box__shadow">
            <ParagrafHome title="Рекомендации"/>
            <Swiper
                // install Swiper modules
                modules={[Navigation, Pagination, A11y]}
                spaceBetween={10}
                slidesPerView={3}
                breakpoints={{
                    1000: {
                      slidesPerView: 3,
                    },
                    500: {
                      slidesPerView: 2,
                    },
                    320: {
                      slidesPerView: 1.4,
                    },
                  }}
                pagination={{ 
                    clickable: true,
                    dynamicBullets: true, 
                }}
                loop = {true}
                >
                    <SwiperNavigations/>
                    {props?.map((item) => (
                        <SwiperSlide key={item.id} className='swiper__slide'>
                          <Link to={item.link}>
                            <div className="recommendationSwiper__item swiper__item schadowImg" style={{ backgroundImage: `url(${item.img})` }}>
                                <h3>{item.title}</h3>
                            </div>
                          </Link>
                        </SwiperSlide>
                    ))}
            </Swiper>
        </div>
    )
};
const SwiperNavigations = () => {
    const swiper = useSwiper();
    return (
      <div className="navigation-btns nav__button">
        <button onClick={() => swiper.slidePrev()} ><img className='buttonPrev' src={nextArrow} alt="" /></button>
        <button onClick={() => swiper.slideNext()} ><img className='buttonNex' src={prewArrow} alt="" /></button>
      </div>
    );
  };