
import { Link } from "react-router-dom";

// componets
import ParagrafHome from "../../componets/paragrafHome/ParagrafHome";


// style
import "./aside.scss"

export default function Aside({props}){
    return (
      <div className="aside__wrapper _body__box__shadow">
        <ParagrafHome title="Новости в тренде" />
        <div className="aside__contant">
          {props?.map((item) => (
            <Link to={item.link}>
              <div className="aside__contant__item" key={item.id}>
                <div className="aside__contant__item__img">
                  <img src={item.img} alt="" />
                </div>
                <h3>{item.title}</h3>
                <p>{item.subtitle}</p>
                <h6>{item.data}</h6>
              </div>
            </Link>
          ))}
        </div>
      </div>
    );
}